import Vue from 'vue';
import Router from 'vue-router';
import config from './config';
import {
  startWithRoot
} from 'src/utils';
import store from 'src/store';
import http from 'src/utils/http';
Vue.use(Router);

function createRoute(routeConfig, path, children) {
  const {
    title,
    component,
    meta = {},
    name
  } = routeConfig;

  return {
    name: name || path,
    path,
    component,
    children,
    meta: {
      ...meta,
      title
    }
  };
}

function travelLayouts(layouts, prefix = '') {
  let routes = [];
  let menus = [];

  layouts.forEach(layout => {
    let {
      children,
      path,
      component
    } = layout;
    let newPath = path;
    let childRoutes = [];

    // 如果不是以根路径开头
    if (!startWithRoot(path)) {
      newPath = prefix + (path ? `/${path}` : '');
    }

    // 处理嵌套路由
    if (children) {
      let result = travelLayouts(children, newPath);
      childRoutes = result.routes;
      layout.children = result.menus;
    }

    // 如果是路由组件
    if (path && component) {
      routes.push(createRoute(layout, newPath, childRoutes));
    } else {
      routes = routes.concat(childRoutes);
    }
    layout.path = newPath;
    menus.push(layout);
  });

  return {
    routes,
    menus
  };
}

function createRoutes(layoutConfig) {
  const layoutTypes = Object.keys(layoutConfig);
  let routes = [];

  layoutTypes.forEach(layout => {
    const result = travelLayouts([layoutConfig[layout]]);
    routes = routes.concat(result.routes);
  });

  return routes;
}

function createMenus(layoutConfig) {
  const result = travelLayouts([layoutConfig.basicLayout]);

  return result.menus;
}

export const menus = createMenus(config);

export const routes = [
  ...createRoutes(config),
  {
    path: '*',
    redirect: '/main/home'
  }
];

const router = new Router({
  routes: routes
});
export const routeMap = {
  1: ['/main/data'],
  2: ['/main/business'],
  5: ['/main/business-manage'],
  6: ['/main/auth'],
  7: ['/main/metadata']
};
const routeDependRole = (allowRoute = [], to, next) => {
  const allow = allowRoute.some(item => {
    return to.path.startsWith(item);
  });
  if (allow) {
    next();
  } else {
    next('/');
  }
};
let allowRoute = [];
const getAllowRoute = (module) => {
  const {modules} = module;
  const routes = modules.reduce((accumulator, item) => {
    if (routeMap[item.moduleId]) {
      return accumulator.concat(routeMap[item.moduleId]);
    } else {
      return accumulator;
    }
  }, []);
  // 加上默认首页路由
  routes.push('/main/home');
  return routes;
};
router.beforeEach((to, from, next) => {
  let app = router.app;
  if (to.name === 'login') {
    next();
  } else {
    if (!Object.keys(store.state.user.modules).length) {
      app.$nextTick(() => {
        store.dispatch('user/authorize').then(data => {
          store.dispatch('user/getModules').then(_ => {
            allowRoute = getAllowRoute(store.state.user.modules);
            routeDependRole(allowRoute, to, next);
          });
        }).catch(e => {
          http.forwardLogin();
        });
      });
    } else {
      routeDependRole(allowRoute, to, next);
    }
  }
});

export default router;



