import http from 'src/utils/http';

export function getCategory() {
  return http.get('/businesses/categories');
}

export function create(param) {
  return http.post('/businesses/categories', param);
}

export function update(param) {
  return http.put(`/businesses/categories/${param.guid}`, param);
}

export function remove(categoryGuid) {
  return http.delete(`/businesses/categories/${categoryGuid}`);
}

export function getTableList(id, params) {
  return http.post(`/businesses/category/relations/${id}`, params);
}

export function findTableList(params) {
  return http.post(`/businesses/relations`, params);
}

export function deleteBusiness(id) {
  return http.delete(`/businesses/${id}`);
}

export function getManageTableList(params) {
  return http.post(`/businessManage`, params);
}

export function getDepartments(params) {
  return http.get(`/businessManage/departments`, params);
}

export function createTableItem(id, param) {
  return http.post(`/businesses/category/${id}`, param);
}

export function getBusinessDetail(id, isManage) {
  const url = isManage ? `/businessManage/${id}` : `/businesses/${id}`;
  return http.get(url);
}

export function getTechDetail(id, isManage) {
  const url = isManage ? `/businessManage/${id}/technical` : `/businesses/${id}/technical`;
  return http.get(url);
}

export function updateBusinessInfo(id, isManage, param) {
  const url = isManage ? `/businessManage/${id}/business` : `/businesses/${id}`;
  return http.put(url, param);
}



