import http from 'src/utils/http';

export function getCategory() {
  return http.get('/technical/category');
}

export function create(param) {
  return http.post('/technical/category', param);
}

export function update(param) {
  return http.post('/technical/update/category', param);
}

export function remove(categoryGuid) {
  return http.delete(`/technical/category/${categoryGuid}`);
}




