import http from 'src/utils/http';

export function getAllMetadataList(params) {
  return http.post(`technical/search/database/${params.categoryGuid}`, params.param);
}

export function getMetadataList(param) {
  return http.post('metadata/search/databases', param);
}

export function getMetadataTable(param) {
  return http.post('metadata/search/table', param);
}

export function getTableList(params) {
  return http.post(`technical/search/table/${params.categoryGuid}`, params.param);
}

export function getColumnList(param) {
  return http.post('metadata/search/column', param);
}

export function getDatabases(param) {
  return http.get('metadata/databases', param);
}

export function getTables({databaseId, offset, limit}) {
  return http.post(`metadata/tables/${databaseId}/${offset}/${limit}`);
}



