import types from 'src/store/mutation-types';
import {
  LOADING,
  SUCCESS,
  FAIL
} from 'src/store/status';
import * as homeService from 'src/service/home';

const {
  GET_HOME_TOTAL,
  GET_HOME_TOTAL_SUCCESS,
  GET_HOME_TOTAL_FAILURE,

  GET_HOME_TABLE_USAGE,
  GET_HOME_TABLE_USAGE_SUCCESS,
  GET_HOME_TABLE_USAGE_FAILURE,

  GET_HOME_CATEGORY_TABLE,
  GET_HOME_CATEGORY_TABLE_SUCCESS,
  GET_HOME_CATEGORY_TABLE_FAILURE,

  GET_HOME_CATEGORY_LIBRARY,
  GET_HOME_CATEGORY_LIBRARY_SUCCESS,
  GET_HOME_CATEGORY_LIBRARY_FAILURE,

  GET_HOME_ROLE,
  GET_HOME_ROLE_SUCCESS,
  GET_HOME_ROLE_FAILURE,

  GET_HOME_ROLE_SELECT,
  GET_HOME_ROLE_SELECT_SUCCESS,
  GET_HOME_ROLE_SELECT_FAILURE,

  GET_HOME_USER,
  GET_HOME_USER_SUCCESS,
  GET_HOME_USER_FAILURE,

  GET_HOME_ACTIVE_BUSINESS,
  GET_HOME_ACTIVE_BUSINESS_SUCCESS,
  GET_HOME_ACTIVE_BUSINESS_FAILURE,

  GET_HOME_TECH_BUSINESS,
  GET_HOME_TECH_BUSINESS_SUCCESS,
  GET_HOME_TECH_BUSINESS_FAILURE,

  GET_HOME_DATABASE_CHART,
  GET_HOME_DATABASE_CHART_SUCCESS,
  GET_HOME_DATABASE_CHART_FAILURE,

  GET_HOME_TABLE_CHART,
  GET_HOME_TABLE_CHART_SUCCESS,
  GET_HOME_TABLE_CHART_FAILURE,

  GET_HOME_TOTAL_BUSINESS_CHART,
  GET_HOME_TOTAL_BUSINESS_CHART_SUCCESS,
  GET_HOME_TOTAL_BUSINESS_CHART_FAILURE,

  GET_HOME_ACTIVE_BUSINESS_CHART,
  GET_HOME_ACTIVE_BUSINESS_CHART_SUCCESS,
  GET_HOME_ACTIVE_BUSINESS_CHART_FAILURE
} = types;

// initial state
const state = {
  baseInfo: {},
  baseInfoStatus: SUCCESS,
  tableUsage: {
    list: [],
    status: SUCCESS
  },
  categoryTable: {
    list: [],
    status: SUCCESS
  },
  categoryLibrary: {
    list: [],
    status: SUCCESS
  },

  dbChart: {
    data: {},
    status: SUCCESS
  },
  tbChart: {
    data: {},
    status: SUCCESS,
    isArea: true
  },
  techBusiness: {
    list: [],
    status: SUCCESS
  },
  activeBusiness: {
    list: [],
    status: SUCCESS
  },
  totalBusinessChart: {
    data: {},
    status: SUCCESS
  },
  activeBusinessChart: {
    data: {},
    status: SUCCESS
  },
  roleList: {
    list: [],
    status: SUCCESS
  },
  roles: {
    list: [],
    status: SUCCESS
  },
  users: {
    list: [],
    status: SUCCESS
  }
};

const actions = {
  async getBaseInfo(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_HOME_TOTAL, params);
      const res = await homeService.getBaseInfo(params);
      commit(GET_HOME_TOTAL_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_HOME_TOTAL_FAILURE, err);
      throw err;
    }
  },

  async getTableUsage(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_HOME_TABLE_USAGE, params);
      const res = await homeService.getTableUsage(params);
      commit(GET_HOME_TABLE_USAGE_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_HOME_TABLE_USAGE_FAILURE, err);
      throw err;
    }
  },

  async getCategoryTable(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_HOME_CATEGORY_TABLE, params);
      const res = await homeService.getCategoryTable(params);
      commit(GET_HOME_CATEGORY_TABLE_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_HOME_CATEGORY_TABLE_FAILURE, err);
      throw err;
    }
  },

  async getCategoryLibrary(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_HOME_CATEGORY_LIBRARY, params);
      const res = await homeService.getCategoryLibrary(params.id, params.param);
      commit(GET_HOME_CATEGORY_LIBRARY_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_HOME_CATEGORY_LIBRARY_FAILURE, err);
      throw err;
    }
  },

  async getDbChart(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_HOME_DATABASE_CHART, params);
      const res = await homeService.getDbChart(params);
      commit(GET_HOME_DATABASE_CHART_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_HOME_DATABASE_CHART_FAILURE, err);
      throw err;
    }
  },

  async getTableChart(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_HOME_TABLE_CHART, params);
      const res = await homeService.getTableChart(params);
      commit(GET_HOME_TABLE_CHART_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_HOME_TABLE_CHART_FAILURE, err);
      throw err;
    }
  },

  async getTechBusiness(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_HOME_TECH_BUSINESS, params);
      const res = await homeService.getTechBusiness(params);
      commit(GET_HOME_TECH_BUSINESS_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_HOME_TECH_BUSINESS_FAILURE, err);
      throw err;
    }
  },

  async getActiveBusiness(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_HOME_ACTIVE_BUSINESS, params);
      const res = await homeService.getActiveBusiness(params);
      commit(GET_HOME_ACTIVE_BUSINESS_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_HOME_ACTIVE_BUSINESS_FAILURE, err);
      throw err;
    }
  },

  async getTotalBusinessChart(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_HOME_TOTAL_BUSINESS_CHART, params);
      const res = await homeService.getTotalBusinessChart(params);
      commit(GET_HOME_TOTAL_BUSINESS_CHART_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_HOME_TOTAL_BUSINESS_CHART_FAILURE, err);
      throw err;
    }
  },

  async getActiveBusinessChart(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_HOME_ACTIVE_BUSINESS_CHART, params);
      const res = await homeService.getActiveBusinessChart(params);
      commit(GET_HOME_ACTIVE_BUSINESS_CHART_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_HOME_ACTIVE_BUSINESS_CHART_FAILURE, err);
      throw err;
    }
  },

  async getHomeRoleSelect(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_HOME_ROLE_SELECT, params);
      const res = await homeService.getHomeRoleSelect(params);
      commit(GET_HOME_ROLE_SELECT_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_HOME_ROLE_SELECT_FAILURE, err);
      throw err;
    }
  },

  async getHomeRole(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_HOME_ROLE, params);
      const res = await homeService.getHomeRole(params);
      commit(GET_HOME_ROLE_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_HOME_ROLE_FAILURE, err);
      throw err;
    }
  },

  async getHomeUser(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_HOME_USER, params);
      const res = await homeService.getHomeUser(params.id, params.param);
      commit(GET_HOME_USER_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_HOME_USER_FAILURE, err);
      throw err;
    }
  }
};

const mutations = {
  [GET_HOME_TOTAL](state) {
    state.baseInfoStatus = LOADING;
  },
  [GET_HOME_TOTAL_SUCCESS](state, payload) {
    state.baseInfoStatus = SUCCESS;
    state.baseInfo = payload;
  },
  [GET_HOME_TOTAL_FAILURE](state) {
    state.baseInfoStatus = FAIL;
  },

  [GET_HOME_TABLE_USAGE](state, params) {
    let list = state.tableUsage.list;

    if (params.offset === 0) {
      list = [];
    }
    state.tableUsage = {
      list,
      status: LOADING
    };
  },
  [GET_HOME_TABLE_USAGE_SUCCESS](state, payload) {
    state.tableUsage = {
      list: payload.lists,
      total: payload.sum,
      status: SUCCESS
    };
  },
  [GET_HOME_TABLE_USAGE_FAILURE](state) {
    state.tableUsage = {
      ...state.tableUsage,
      status: FAIL
    };
  },

  [GET_HOME_CATEGORY_TABLE](state, params) {
    let list = state.categoryTable.list;

    if (params.offset === 0) {
      list = [];
    }
    state.categoryTable = {
      list,
      status: LOADING
    };
  },
  [GET_HOME_CATEGORY_TABLE_SUCCESS](state, payload) {
    state.categoryTable = {
      list: payload.lists,
      total: payload.sum,
      status: SUCCESS
    };
  },
  [GET_HOME_CATEGORY_TABLE_FAILURE](state) {
    state.categoryTable = {
      ...state.categoryTable,
      status: FAIL
    };
  },

  [GET_HOME_CATEGORY_LIBRARY](state, params) {
    let list = state.categoryLibrary.list;

    if (params.offset === 0) {
      list = [];
    }
    state.categoryLibrary = {
      list,
      status: LOADING
    };
  },
  [GET_HOME_CATEGORY_LIBRARY_SUCCESS](state, payload) {
    state.categoryLibrary = {
      list: payload.lists,
      total: payload.sum,
      status: SUCCESS
    };
  },
  [GET_HOME_CATEGORY_LIBRARY_FAILURE](state) {
    state.categoryLibrary = {
      ...state.categoryLibrary,
      status: FAIL
    };
  },

  [GET_HOME_DATABASE_CHART](state) {
    const data = state.dbChart.data;
    state.dbChart = {
      data,
      status: LOADING
    };
  },
  [GET_HOME_DATABASE_CHART_SUCCESS](state, payload) {
    state.dbChart = {
      data: payload,
      status: SUCCESS
    };
  },
  [GET_HOME_DATABASE_CHART_FAILURE](state) {
    state.dbChart = {
      ...state.dbChart,
      status: FAIL
    };
  },

  [GET_HOME_TABLE_CHART](state) {
    const data = state.tbChart.data;
    state.tbChart = {
      data,
      status: LOADING
    };
  },
  [GET_HOME_TABLE_CHART_SUCCESS](state, payload) {
    state.tbChart = {
      data: payload,
      status: SUCCESS,
      isArea: true
    };
  },
  [GET_HOME_TABLE_CHART_FAILURE](state) {
    state.tbChart = {
      ...state.tbChart,
      status: FAIL
    };
  },

  [GET_HOME_TECH_BUSINESS](state) {
    const list = state.techBusiness.list;
    state.techBusiness = {
      list,
      status: LOADING
    };
  },
  [GET_HOME_TECH_BUSINESS_SUCCESS](state, payload) {
    state.techBusiness = {
      list: payload,
      status: SUCCESS
    };
  },
  [GET_HOME_TECH_BUSINESS_FAILURE](state) {
    state.techBusiness = {
      ...state.techBusiness,
      status: FAIL
    };
  },

  [GET_HOME_ACTIVE_BUSINESS](state) {
    const list = state.activeBusiness.list;
    state.activeBusiness = {
      list,
      status: LOADING
    };
  },
  [GET_HOME_ACTIVE_BUSINESS_SUCCESS](state, payload) {
    state.activeBusiness = {
      list: payload,
      status: SUCCESS
    };
  },
  [GET_HOME_ACTIVE_BUSINESS_FAILURE](state) {
    state.activeBusiness = {
      ...state.activeBusiness,
      status: FAIL
    };
  },

  [GET_HOME_TOTAL_BUSINESS_CHART](state) {
    const data = state.totalBusinessChart.data;
    state.totalBusinessChart = {
      data,
      status: LOADING
    };
  },
  [GET_HOME_TOTAL_BUSINESS_CHART_SUCCESS](state, payload) {
    state.totalBusinessChart = {
      data: payload,
      status: SUCCESS
    };
  },
  [GET_HOME_TOTAL_BUSINESS_CHART_FAILURE](state) {
    state.totalBusinessChart = {
      ...state.totalBusinessChart,
      status: FAIL
    };
  },

  [GET_HOME_ACTIVE_BUSINESS_CHART](state) {
    const data = state.activeBusinessChart.data;
    state.activeBusinessChart = {
      data,
      status: LOADING
    };
  },
  [GET_HOME_ACTIVE_BUSINESS_CHART_SUCCESS](state, payload) {
    state.activeBusinessChart = {
      data: payload,
      status: SUCCESS
    };
  },
  [GET_HOME_ACTIVE_BUSINESS_CHART_FAILURE](state) {
    state.activeBusinessChart = {
      ...state.activeBusinessChart,
      status: FAIL
    };
  },

  [GET_HOME_ROLE](state, params) {
    let list = state.roles.list;

    if (params.offset === 0) {
      list = [];
    }
    state.roles = {
      list,
      status: LOADING
    };
  },
  [GET_HOME_ROLE_SUCCESS](state, payload) {
    state.roles = {
      list: payload.lists,
      total: payload.sum,
      status: SUCCESS
    };
  },
  [GET_HOME_ROLE_FAILURE](state) {
    state.roles = {
      ...state.roles,
      status: FAIL
    };
  },

  [GET_HOME_ROLE_SELECT](state, params) {
    let list = state.roleList.list;

    state.roleList = {
      list,
      status: LOADING
    };
  },
  [GET_HOME_ROLE_SELECT_SUCCESS](state, payload) {
    state.roleList = {
      list: payload,
      status: SUCCESS
    };
  },
  [GET_HOME_ROLE_SELECT_FAILURE](state) {
    state.roleList = {
      ...state.roleList,
      status: FAIL
    };
  },

  [GET_HOME_USER](state, params) {
    let list = state.users.list;

    if (params.offset === 0) {
      list = [];
    }
    state.users = {
      list,
      status: LOADING
    };
  },
  [GET_HOME_USER_SUCCESS](state, payload) {
    state.users = {
      list: payload.lists,
      total: payload.sum,
      status: SUCCESS
    };
  },
  [GET_HOME_USER_FAILURE](state) {
    state.users = {
      ...state.users,
      status: FAIL
    };
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
};



