import types from 'src/store/mutation-types';
import {
  LOADING,
  SUCCESS,
  FAIL
} from 'src/store/status';
import {
  list2tree,
  sortTreeList,
  deleteInTree
} from 'src/utils';
import * as technicalService from 'src/service/business-obj/technology';

const {
  GET_TECH_CATEGORY,
  GET_TECH_CATEGORY_SUCCESS,
  GET_TECH_CATEGORY_FAILURE,

  UPDATE_TECH_INFO,
  UPDATE_TECH_INFO_SUCCESS,
  UPDATE_TECH_INFO_FAILURE,

  GET_BUSINESS_RELATION,
  GET_BUSINESS_RELATION_SUCCESS,
  GET_BUSINESS_RELATION_FAILURE,

  SEARCH_BUSINESS_RELATION,
  SEARCH_BUSINESS_RELATION_SUCCESS,
  SEARCH_BUSINESS_RELATION_FAILURE
} = types;

// initial state
const state = {
  categories: {
    list: [],
    status: LOADING
  },
  categoryMap: {},
  relations: {},
  currentCategoryGuid: null,

  techUpdateStatus: SUCCESS,
  currentRelations: {
    list: [],
    total: 0
  }, // 当前选中节点的当前页关联列表
  searchRelations: {
    list: [],
    total: 0
  },
  relationsStatus: SUCCESS
};

const actions = {
  async getTopLevelCategory(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_TECH_CATEGORY);
      const res = await technicalService.getCategory(params);
      // metaspace二期接口返回字段调整，手动添加一期字段categoryGuid，避免修改量过大
      res.forEach(item => {
        item.categoryGuid = item.guid;
        item.disabled = !item.show;
      });
      const categoryMap = {};
      const unsortCategories = list2tree(res, 'categoryGuid', 'parentCategoryGuid');
      const allCategories = sortTreeList(unsortCategories, 'categoryGuid', 'upBrotherCategoryGuid', 'downBrotherCategoryGuid');
      const categories = deleteInTree(allCategories, true, 'hide');
      res.forEach(item => {
        categoryMap[item.categoryGuid] = item;
      });
      commit(GET_TECH_CATEGORY_SUCCESS, { categories, categoryMap });
      return categories;
    } catch (err) {
      commit(GET_TECH_CATEGORY_FAILURE, err);
      throw err;
    }
  },

  async getRelations(context, {categoryGuid, param, isManage}) {
    const {
      commit
    } = context;

    try {
      commit(GET_BUSINESS_RELATION, categoryGuid);
      const res = await technicalService.getRelations(categoryGuid, isManage, param);
      // 前端添加字段，为了较少改动一期代码
      res.categoryGuid = categoryGuid;
      commit(GET_BUSINESS_RELATION_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_BUSINESS_RELATION_FAILURE, err);
      throw err;
    }
  },

  async findRelations(context, {filterTableName, isManage, param}) {
    const {
      commit
    } = context;

    try {
      commit(SEARCH_BUSINESS_RELATION, filterTableName);
      const res = await technicalService.findRelations(isManage, { filterTableName, ...param });
      commit(SEARCH_BUSINESS_RELATION_SUCCESS, res);
      return res;
    } catch (err) {
      commit(SEARCH_BUSINESS_RELATION_FAILURE, err);
      throw err;
    }
  },

  async updateTechInfo(context, params) {
    const {
      commit
    } = context;

    try {
      commit(UPDATE_TECH_INFO, params);
      const res = await technicalService.updateTechInfo(params.id, params.isManage, params.param);
      commit(UPDATE_TECH_INFO_SUCCESS, res);
      return res;
    } catch (err) {
      commit(UPDATE_TECH_INFO_FAILURE, err);
      throw err;
    }
  }
};

const mutations = {
  // 获取一级目录
  [GET_TECH_CATEGORY](state) {
    state.categories = {
      ...state.categories,
      status: LOADING
    };
  },
  [GET_TECH_CATEGORY_SUCCESS](state, payload) {
    state.categories = {
      list: payload.categories,
      status: SUCCESS
    };
    state.categoryMap = payload.categoryMap;
  },
  [GET_TECH_CATEGORY_FAILURE](state) {
    state.categories = {
      ...state.categories,
      status: FAIL
    };
  },

  // 获取分层和表的分页关联关系
  [GET_BUSINESS_RELATION](state) {
    state.relationsStatus = LOADING;
  },
  [GET_BUSINESS_RELATION_SUCCESS](state, payload) {
    state.currentCategoryGuid = payload.categoryGuid;
    state.currentRelations = {
      list: payload.lists,
      total: payload.sum
    };
    state.relationsStatus = SUCCESS;
  },
  [GET_BUSINESS_RELATION_FAILURE](state) {
    state.relationsStatus = FAIL;
  },

  // 搜索表的关联关系
  [SEARCH_BUSINESS_RELATION](state) {
    state.relationsStatus = LOADING;
  },
  [SEARCH_BUSINESS_RELATION_SUCCESS](state, payload) {
    state.searchRelations = {
      list: payload.lists,
      total: payload.sum
    };
    state.relationsStatus = SUCCESS;
  },
  [SEARCH_BUSINESS_RELATION_FAILURE](state) {
    state.relationsStatus = FAIL;
  },

  [UPDATE_TECH_INFO](state, params) {
    state.techUpdateStatus = LOADING;
  },
  [UPDATE_TECH_INFO_SUCCESS](state, payload) {
    state.techUpdateStatus = SUCCESS;
  },
  [UPDATE_TECH_INFO_FAILURE](state) {
    state.techUpdateStatus = FAIL;
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
};



