import types from 'src/store/mutation-types';
import {
  LOADING,
  SUCCESS,
  FAIL
} from 'src/store/status';
import * as metadataService from 'src/service/metadata-management/metadata-management-list';

const {
  GET_METADATA,
  GET_METADATA_SUCCESS,
  GET_METADATA_FAILURE,

  GET_TABLE,
  GET_TABLE_SUCCESS,
  GET_TABLE_FAILURE,

  GET_DATABASES,
  GET_DATABASES_SUCCESS,
  GET_DATABASES_FAILURE
} = types;

// initial state
const state = {
  metadatas: {
    list: [],
    total: 0,
    status: SUCCESS
  },
  tables: {
    list: [],
    total: 0,
    status: SUCCESS
  },
  // 供新建表使用的库下拉列表
  databases: {
    list: [],
    total: 0,
    status: SUCCESS
  }
};

const actions = {
  async getMetadatas(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_METADATA, params);
      const {query, offset, limit, isSearch} = params;
      const res = await metadataService.getMetadataList({query, offset, limit});
      commit(GET_METADATA_SUCCESS, {res, isSearch});
      return res;
    } catch (err) {
      commit(GET_METADATA_FAILURE, err);
      throw err;
    }
  },

  async getAllMetadataList(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_METADATA, params);
      const {categoryGuid, query, offset, limit, isSearch} = params;
      const res = await metadataService.getAllMetadataList({categoryGuid, param: {query, offset, limit}});
      commit(GET_METADATA_SUCCESS, {res, isSearch});
      return res;
    } catch (err) {
      commit(GET_METADATA_FAILURE, err);
      throw err;
    }
  },

  async getTableList(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_TABLE, params);
      const {categoryGuid, query, offset, limit, isSearch} = params;
      const res = await metadataService.getTableList({categoryGuid, param: {query, offset, limit}});
      commit(GET_TABLE_SUCCESS, {res, isSearch});
      return res;
    } catch (err) {
      commit(GET_TABLE_FAILURE, err);
      throw err;
    }
  },

  async getDatabases(context) {
    const {
      commit
    } = context;

    try {
      commit(GET_DATABASES);
      const res = await metadataService.getDatabases();
      commit(GET_DATABASES_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_DATABASES_FAILURE, err);
      throw err;
    }
  }
};

const mutations = {
  [GET_METADATA](state, params) {
    let list = state.metadatas.list;

    if (params.offset === 0) {
      list = [];
    }
    state.metadatas = {
      list,
      status: LOADING
    };
  },
  [GET_METADATA_SUCCESS](state, {res, isSearch}, rootState) {
    const data = res.lists.map(item => {
      const tableList = item.tableList || [];

      return {
        value: item.databaseId,
        label: item.databaseName,
        disabled: tableList.length === 0 || item.status === 'DELETED',
        children: tableList.map(item => {
          return {
            tableGuid: item.tableId,
            tableName: item.tableName,
            dbName: item.databaseName,
            status: item.status,
            value: item.tableId,
            label: item.tableName,
            disabled: item.status === 'DELETED',
            databaseName: item.databaseName,
            createTime: item.createTime
          };
        })
      };
    });
    console.log(data);
    const list = state.metadatas.list.concat(data);

    state.metadatas = {
      list: isSearch ? data : list,
      total: res.sum,
      status: SUCCESS
    };
  },
  [GET_METADATA_FAILURE](state) {
    state.metadatas = {
      ...state.metadatas,
      status: FAIL
    };
  },

  [GET_TABLE](state, params) {
    let list = state.tables.list;

    if (params.offset === 0) {
      list = [];
    }
    state.tables = {
      list,
      status: LOADING
    };
  },
  [GET_TABLE_SUCCESS](state, {res, isSearch}, rootState) {
    const data = res.lists.map(item => {
      return {
        tableGuid: item.tableId,
        tableName: item.tableName,
        dbName: item.databaseName,
        status: item.status,
        value: item.tableId,
        label: item.tableName,
        disabled: item.status === 'DELETED',
        databaseName: item.databaseName,
        createTime: item.createTime
      };
    });
    const list = state.tables.list.concat(data);

    state.tables = {
      list: isSearch ? data : list,
      total: res.sum,
      status: SUCCESS
    };
  },
  [GET_TABLE_FAILURE](state) {
    state.tables = {
      ...state.tables,
      status: FAIL
    };
  },
  [GET_DATABASES](state, params) {
    state.databases = {
      list: [],
      status: LOADING
    };
  },
  [GET_DATABASES_SUCCESS](state, payload, rootState) {
    const list = payload.map(item => {
      return {
        value: item,
        label: item
      };
    });

    state.databases = {
      list,
      total: list.length,
      status: SUCCESS
    };
  },
  [GET_DATABASES_FAILURE](state) {
    state.databases = {
      ...state.databases,
      status: FAIL
    };
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
};



