import http from 'src/utils/http';

export function getCategory(isManage) {
  const url = isManage ? `/businessManage/technical/categories` : `/businesses/technical/categories`;
  return http.get(url);
}

export function updateTechInfo(id, isManage, param) {
  const url = isManage ? `/businessManage/${id}` : `/businesses/${id}/technical`;
  return http.put(url, param);
}

export function getRelations(categoryGuid, isManage, param) {
  const url = isManage ? `/businessManage/technical/${categoryGuid}` : `/businesses/technical/${categoryGuid}`;
  return http.post(url, param);
}

export function findRelations(isManage, param) {
  const url = isManage ? `/businessManage/technical/table/relations` : `/businesses/technical/table/relations`;
  return http.post(url, param);
}

export function getTableInfo(isManage, id) {
  const url = isManage ? `businessManage/table/${id}` : `businesses/table/${id}`;
  return http.get(url);
}



