import http from 'src/utils/http';

export function getBaseInfo() {
  return http.get('home/total');
}

export function getTableUsage(param) {
  return http.post('home/table/rank', param);
}

export function getCategoryTable(param) {
  return http.post('home/source/system', param);
}

export function getCategoryLibrary(id, param) {
  return http.post(`home/source/system/${id}`, param);
}

export function getDbChart(param) {
  return http.get('home/database/total', param);
}

export function getTableChart(param) {
  return http.get('home/table/total', param);
}

export function getTechBusiness(param) {
  return http.get('home/business/supplement', param);
}

export function getActiveBusiness(param) {
  return http.post('home/business/available', param);
}

export function getTotalBusinessChart(param) {
  return http.get('home/business/total', param);
}

export function getActiveBusinessChart(param) {
  return http.get('home/business/available/total', param);
}

export function getHomeRoleSelect(param) {
  return http.get('home/roles', param);
}

export function getHomeRole(param) {
  return http.post('home/role/rank', param);
}

export function getHomeUser(id, param) {
  return http.post(`home/role/${id}/users`, param);
}



