import BasicLayout from 'src/components/basic-layout';

// 首页
const Home = () => import('src/views/home');

// 元数据管理
const MetadataManagement = () => import('src/views/metadata-management');
const MetadataManagementDetail = () => import('src/views/metadata-management/detail');
const TableCreate = () => import('src/views/metadata-management/table-create');

// 数据管理
const TableList = () => import('src/views/data-management/table-list');
// const ContentAnalysis = () => import('src/views/data-management/content-analysis');

// 文件管理
// const FileManagement = () => import('src/views/file-management');

// 业务对象
const BusinessObject = () => import('src/views/business-object/table-list');
const BusinessDetail = () => import('src/views/business-object/detail');

// 业务对象管理
const BusinessManageMent = () => import('src/views/business-management/table-list');
const BusinessManageMentDetail = () => import('src/views/business-management/detail');

// 异常
const NotFoundPage = () => import('src/views/exception/404');
const ErrorPage = () => import('src/views/exception/500');

const Auth = () => import('src/views/auth/index');
const UserManagement = () => import('src/views/auth/user');
const RoleManagement = () => import('src/views/auth/role');
const AuthManagement = () => import('src/views/auth/auth');
const UserManagementDetail = () => import('src/views/auth/user/detail');
const RoleManagementDetail = () => import('src/views/auth/role/detail/index');
export default {
  basicLayout: {
    title: '首页',
    path: '/main',
    component: BasicLayout,
    children: [
      {
        title: '首页',
        icon: ' iconfont icon-shouye',
        path: 'home',
        component: Home
      },
      {
        title: '技术数据',
        icon: ' iconfont icon-shuju',
        path: 'data',
        component: TableList
      },
      // {
      //   title: '数据管理',
      //   hide: true,
      //   path: 'data/analysis',
      //   component: ContentAnalysis,
      //   meta: {
      //     index: '/main/data'
      //   }
      // },
      {
        title: '元数据管理',
        icon: ' iconfont icon-yuanshuju',
        path: 'metadata',
        component: MetadataManagement
      },
      {
        title: '新建离线表',
        icon: '',
        hide: true,
        path: 'metadata/create',
        component: TableCreate,
        meta: {index: '/main/metadata'}
      },
      {
        title: '元数据详情',
        icon: '',
        hide: true,
        path: 'metadata/detail/:id',
        component: MetadataManagementDetail,
        meta: {index: '/main/metadata'}
      },
      // {
      //   title: '文件管理',
      //   icon: ' iconfont icon-wenjianjia1',
      //   path: 'file',
      //   component: FileManagement
      // },
      {
        title: '业务对象',
        icon: ' iconfont icon-yewu',
        path: 'business',
        component: BusinessObject
      },
      {
        title: '业务对象详情',
        icon: '',
        hide: true,
        path: 'business/detail/:id',
        component: BusinessDetail,
        meta: {index: '/main/business'}
      },
      {
        title: '业务对象管理',
        icon: ' iconfont icon-neirongguanli',
        path: 'business-manage',
        children: [
          {
            title: '待补充',
            path: 'unsupplemental',
            name: 'unsupplementalList',
            component: BusinessManageMent,
            meta: {index: '/main/business-manage/unsupplemental'}
          },
          {
            title: '已补充',
            path: 'supplemental',
            name: 'supplementalList',
            component: BusinessManageMent,
            meta: {index: '/main/business-manage/supplemental'}
          }
        ]
      },
      {
        title: '业务对象管理已补充详情',
        icon: '',
        hide: true,
        path: 'business-manage/supplemental/detail/:id',
        component: BusinessManageMentDetail,
        name: 'supplementalDetail',
        meta: {index: '/main/business-manage/supplemental'}
      },
      {
        title: '业务对象管理待补充详情',
        icon: '',
        hide: true,
        path: 'business-manage/unsupplemental/detail/:id',
        component: BusinessManageMentDetail,
        name: 'unsupplementalDetail',
        meta: {index: '/main/business-manage/unsupplemental'}
      },
      {
        title: '异常页',
        icon: 'bars',
        path: 'exception',
        hide: true,
        children: [
          {
            title: '404',
            path: '404',
            component: NotFoundPage
          },
          {
            title: '500',
            path: '500',
            component: ErrorPage
          }
        ]
      },
      {
        title: '权限',
        icon: ' iconfont icon-quanxian',
        path: 'auth',
        component: Auth,
        children: [
          {
            title: '用户管理',
            path: 'userManagement',
            component: UserManagement,
            meta: {
              index: '/main/auth/userManagement'
            }
          },
          {
            hide: true,
            path: 'userManagement/:id',
            component: UserManagementDetail,
            meta: {
              index: '/main/auth/userManagement',
              backPath: '/main/auth/userManagement',
              currentLevel: '用户详情',
              upperLevel: '用户管理'
            }
          },
          {
            title: '角色管理',
            path: 'roleManagement',
            component: RoleManagement,
            meta: {
              index: '/main/auth/roleManagement'
            }
          },
          {
            hide: true,
            path: 'roleManagement/:id',
            component: RoleManagementDetail,
            meta: {
              index: '/main/auth/roleManagement',
              backPath: '/main/auth/roleManagement',
              currentLevel: '角色详情',
              upperLevel: '角色管理'
            }
          },
          {
            title: '权限管理',
            path: 'authManagement',
            component: AuthManagement,
            meta: {
              index: '/main/auth/authManagement'
            }
          }
        ]
      }
    ]
  }
};



