import types from 'src/store/mutation-types';
import {
  LOADING,
  SUCCESS,
  FAIL
} from 'src/store/status';
import {
  list2tree,
  deleteInTree,
  updateInTree,
  sortTreeList,
  addTreeItem
} from 'src/utils';
import * as categoryService from 'src/service/business-obj/business';

const {
  GET_BUSINESS_CATEGORY,
  GET_BUSINESS_CATEGORY_SUCCESS,
  GET_BUSINESS_CATEGORY_FAILURE,

  CREATE_BUSINESS_CATEGORY,
  CREATE_BUSINESS_CATEGORY_SUCCESS,
  CREATE_BUSINESS_CATEGORY_FAILURE,

  UPLEVEL_BUSINESS_CATEGORY,
  UPLEVEL_BUSINESS_CATEGORY_SUCCESS,
  UPLEVEL_BUSINESS_CATEGORY_FAILURE,

  UPDATE_BUSINESS_CATEGORY,
  UPDATE_BUSINESS_CATEGORY_SUCCESS,
  UPDATE_BUSINESS_CATEGORY_FAILURE,

  DELETE_BUSINESS_CATEGORY,
  DELETE_BUSINESS_CATEGORY_SUCCESS,
  DELETE_BUSINESS_CATEGORY_FAILURE,

  GET_BUSINESS_LIST,
  GET_BUSINESS_LIST_SUCCESS,
  GET_BUSINESS_LIST_FAILURE,

  SEARCH_BUSINESS_LIST,
  SEARCH_BUSINESS_LIST_SUCCESS,
  SEARCH_BUSINESS_LIST_FAILURE,

  GET_BUSINESS_MANAGE_LIST,
  GET_BUSINESS_MANAGE_LIST_SUCCESS,
  GET_BUSINESS_MANAGE_LIST_FAILURE,

  GET_BUSINESS_DEPARTMENTS,
  GET_BUSINESS_DEPARTMENTS_SUCCESS,
  GET_BUSINESS_DEPARTMENTS_FAILURE,

  CREATE_BUSINESS_ITEM,
  CREATE_BUSINESS_ITEM_SUCCESS,
  CREATE_BUSINESS_ITEM_FAILURE,

  DELETE_BUSINESS_ITEM,
  DELETE_BUSINESS_ITEM_SUCCESS,
  DELETE_BUSINESS_ITEM_FAILURE,

  GET_BUSINESS_DETAIL,
  GET_BUSINESS_DETAIL_SUCCESS,
  GET_BUSINESS_DETAIL_FAILURE,

  GET_TECH_DETAIL,
  GET_TECH_DETAIL_SUCCESS,
  GET_TECH_DETAIL_FAILURE,

  UPDATE_BUSINESS_INFO,
  UPDATE_BUSINESS_INFO_SUCCESS,
  UPDATE_BUSINESS_INFO_FAILURE
} = types;

// initial state
const state = {
  categories: {
    list: [],
    status: LOADING
  },
  categoryMap: {},
  relations: {},
  categoryCreateStatus: SUCCESS,
  categoryUpdateStatus: SUCCESS,
  categoryDeleteStatus: SUCCESS,

  tables: {
    list: [],
    total: 0
  },
  searchTables: {
    list: [],
    total: 0
  },
  tablesStatus: SUCCESS,
  tableItemCreateStatus: SUCCESS,
  deleteStatus: SUCCESS,

  businessDetail: {
    data: {},
    status: LOADING
  },
  businessUpdateStatus: SUCCESS,

  management: {
    list: [],
    total: 0,
    status: SUCCESS
  },
  departments: {
    list: [],
    status: SUCCESS
  },
  techDetail: {
    data: {},
    status: LOADING
  }
};

function res2category(res) {
  const {
    guid,
    name,
    status,
    show,
    parentCategoryGuid,
    description,
    upBrotherCategoryGuid,
    downBrotherCategoryGuid
  } = res;

  return {
    categoryGuid: guid,
    name,
    status,
    show,
    disabled: !show,
    parentCategoryGuid,
    description,
    upBrotherCategoryGuid,
    downBrotherCategoryGuid
  };
}

const actions = {
  async getTopLevelCategory(context) {
    const {
      commit
    } = context;

    try {
      commit(GET_BUSINESS_CATEGORY);
      const res = await categoryService.getCategory();
      // metaspace二期接口返回字段调整，手动添加一期字段categoryGuid，避免修改量过大
      res.forEach(item => {
        item.categoryGuid = item.guid;
        item.disabled = !item.show;
      });
      const categoryMap = {};
      const unsortCategories = list2tree(res, 'categoryGuid', 'parentCategoryGuid');
      const allCategories = sortTreeList(unsortCategories, 'categoryGuid', 'upBrotherCategoryGuid', 'downBrotherCategoryGuid');
      const categories = deleteInTree(allCategories, true, 'hide');
      res.forEach(item => {
        categoryMap[item.categoryGuid] = item;
      });
      commit(GET_BUSINESS_CATEGORY_SUCCESS, { categories, categoryMap });
      return categories;
    } catch (err) {
      commit(GET_BUSINESS_CATEGORY_FAILURE, err);
      throw err;
    }
  },

  async createCategory(context, params) {
    const {
      commit
    } = context;

    try {
      commit(CREATE_BUSINESS_CATEGORY, params);
      const res = await categoryService.create(params);
      const category = res2category(res);
      commit(CREATE_BUSINESS_CATEGORY_SUCCESS, category);
      return category;
    } catch (err) {
      commit(CREATE_BUSINESS_CATEGORY_FAILURE, err);
      throw err;
    }
  },

  async updateCategory(context, params) {
    const {
      commit
    } = context;

    try {
      commit(UPDATE_BUSINESS_CATEGORY, params);
      const res = await categoryService.update(params);
      const category = res2category(res);
      commit(UPDATE_BUSINESS_CATEGORY_SUCCESS, category);
      return category;
    } catch (err) {
      commit(UPDATE_BUSINESS_CATEGORY_FAILURE, err);
      throw err;
    }
  },

  async deleteCategory(context, categoryGuid) {
    const {
      commit
    } = context;

    try {
      commit(DELETE_BUSINESS_CATEGORY, categoryGuid);
      await categoryService.remove(categoryGuid);
      commit(DELETE_BUSINESS_CATEGORY_SUCCESS, categoryGuid);
      return categoryGuid;
    } catch (err) {
      commit(DELETE_BUSINESS_CATEGORY_FAILURE, categoryGuid);
      throw err;
    }
  },

  async getTableList(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_BUSINESS_LIST, params);
      const res = await categoryService.getTableList(params.id, params.param);
      commit(GET_BUSINESS_LIST_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_BUSINESS_LIST_FAILURE, params);
      throw err;
    }
  },

  async findTableList(context, params) {
    const {
      commit
    } = context;

    try {
      commit(SEARCH_BUSINESS_LIST, params);
      const res = await categoryService.findTableList(params);
      commit(SEARCH_BUSINESS_LIST_SUCCESS, res);
      return res;
    } catch (err) {
      commit(SEARCH_BUSINESS_LIST_FAILURE, params);
      throw err;
    }
  },

  async deleteBusiness(context, id) {
    const {
      commit
    } = context;

    try {
      commit(DELETE_BUSINESS_ITEM, id);
      await categoryService.deleteBusiness(id);
      commit(DELETE_BUSINESS_ITEM_SUCCESS, id);
      return id;
    } catch (err) {
      commit(DELETE_BUSINESS_ITEM_FAILURE, id);
      throw err;
    }
  },

  async getManageTableList(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_BUSINESS_MANAGE_LIST, params);
      const res = await categoryService.getManageTableList(params);
      commit(GET_BUSINESS_MANAGE_LIST_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_BUSINESS_MANAGE_LIST_FAILURE, params);
      throw err;
    }
  },

  async getDepartments(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_BUSINESS_DEPARTMENTS, params);
      const res = await categoryService.getDepartments(params);
      commit(GET_BUSINESS_DEPARTMENTS_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_BUSINESS_DEPARTMENTS_FAILURE, params);
      throw err;
    }
  },

  async createTableItem(context, params) {
    const {
      commit
    } = context;

    try {
      commit(CREATE_BUSINESS_ITEM, params);
      await categoryService.createTableItem(params.id, params.param);
      commit(CREATE_BUSINESS_ITEM_SUCCESS, params);
      return params;
    } catch (err) {
      commit(CREATE_BUSINESS_ITEM_FAILURE, params);
      throw err;
    }
  },

  async getBusinessDetail(context, {params, isManage}) {
    const {
      commit
    } = context;

    try {
      commit(GET_BUSINESS_DETAIL, params);
      const res = await categoryService.getBusinessDetail(params, isManage);
      commit(GET_BUSINESS_DETAIL_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_BUSINESS_DETAIL_FAILURE, params);
      throw err;
    }
  },

  async getTechDetail(context, {params, isManage}) {
    const {
      commit
    } = context;

    try {
      commit(GET_TECH_DETAIL, params);
      const res = await categoryService.getTechDetail(params, isManage);
      commit(GET_TECH_DETAIL_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_TECH_DETAIL_FAILURE, params);
      throw err;
    }
  },

  async updateBusinessInfo(context, {params, isManage}) {
    const {
      commit
    } = context;

    try {
      commit(UPDATE_BUSINESS_INFO, params);
      const res = await categoryService.updateBusinessInfo(params.id, isManage, params.param);
      commit(UPDATE_BUSINESS_INFO_SUCCESS, res);
      return res;
    } catch (err) {
      commit(UPDATE_BUSINESS_INFO_FAILURE, err);
      throw err;
    }
  }
};

const mutations = {
  // 获取一级目录
  [GET_BUSINESS_CATEGORY](state) {
    state.categories = {
      ...state.categories,
      status: LOADING
    };
  },
  [GET_BUSINESS_CATEGORY_SUCCESS](state, payload) {
    state.categories = {
      list: payload.categories,
      status: SUCCESS
    };
    state.categoryMap = payload.categoryMap;
  },
  [GET_BUSINESS_CATEGORY_FAILURE](state) {
    state.categories = {
      ...state.categories,
      status: FAIL
    };
  },

  // 新建目录
  [CREATE_BUSINESS_CATEGORY](state) {
    state.categoryCreateStatus = LOADING;
  },
  [CREATE_BUSINESS_CATEGORY_SUCCESS](state, payload) {
    const {
      categoryMap,
      categories
    } = state;
    let list = categories.list;
    const parentCategoryGuid = payload.parentCategoryGuid;

    if (parentCategoryGuid) {
      const category = categoryMap[parentCategoryGuid];
      const children = category.children || [];

      payload.level = category.level + 1;
      category.children = addTreeItem(children, payload, 'categoryGuid', 'upBrotherCategoryGuid', 'downBrotherCategoryGuid');
    } else {
      payload.level = 0;
      list = addTreeItem(list, payload, 'categoryGuid', 'upBrotherCategoryGuid', 'downBrotherCategoryGuid');
    }
    state.categories = {
      ...categories,
      list: [ ...list ]
    };
    state.categoryMap = {
      ...categoryMap,
      [payload.categoryGuid]: payload
    };
    state.categoryCreateStatus = SUCCESS;
  },
  [CREATE_BUSINESS_CATEGORY_FAILURE](state) {
    state.categoryCreateStatus = FAIL;
  },

  // 新建同级目录
  [UPLEVEL_BUSINESS_CATEGORY](state) {
    state.categoryCreateStatus = LOADING;
  },
  [UPLEVEL_BUSINESS_CATEGORY_SUCCESS](state) {
    state.categoryCreateStatus = SUCCESS;
  },
  [UPLEVEL_BUSINESS_CATEGORY_FAILURE](state) {
    state.categoryCreateStatus = FAIL;
  },

  // 更新目录
  [UPDATE_BUSINESS_CATEGORY](state) {
    state.categoryUpdateStatus = LOADING;
  },
  [UPDATE_BUSINESS_CATEGORY_SUCCESS](state, payload) {
    const {
      categoryMap,
      categories
    } = state;
    const categoryGuid = payload.categoryGuid;
    state.categoryMap = {
      ...categoryMap,
      [categoryGuid]: {
        ...categoryMap[categoryGuid],
        ...payload
      }
    };
    state.categories = {
      ...categories,
      list: updateInTree(categories.list, payload, 'categoryGuid')
    };
    state.categoryUpdateStatus = SUCCESS;
  },
  [UPDATE_BUSINESS_CATEGORY_FAILURE](state) {
    state.categoryUpdateStatus = FAIL;
  },

  // 删除目录
  [DELETE_BUSINESS_CATEGORY](state, payload) {
    const {
      categoryMap,
      categories
    } = state;
    const category = categoryMap[payload];

    categoryMap[payload] = {
      ...category,
      deleting: true
    };
    state.categories = {
      ...categories,
      list: updateInTree(categories.list, categoryMap[payload], 'categoryGuid')
    };
    state.categoryDeleteStatus = LOADING;
  },
  [DELETE_BUSINESS_CATEGORY_SUCCESS](state, payload) {
    const {
      categories
    } = state;

    state.categoryMap = {
      ...state.categoryMap,
      [payload]: null
    };
    state.categories = {
      ...categories,
      list: deleteInTree(categories.list, payload, 'categoryGuid')
    };
    state.categoryDeleteStatus = SUCCESS;
  },
  [DELETE_BUSINESS_CATEGORY_FAILURE](state, payload) {
    const {
      categoryMap,
      categories
    } = state;
    const category = categoryMap[payload];

    categoryMap[payload] = {
      ...category,
      deleting: false
    };
    state.categories = {
      ...categories,
      list: updateInTree(categories.list, categoryMap[payload], 'categoryGuid')
    };
    state.categoryDeleteStatus = FAIL;
  },

  [GET_BUSINESS_LIST](state, params) {
    let list = state.tables.list;

    if (params.offset === 0) {
      list = [];
    }
    state.tables = {
      list
    };
    state.tablesStatus = LOADING;
  },
  [GET_BUSINESS_LIST_SUCCESS](state, payload) {
    state.tables = {
      list: payload.lists,
      total: payload.sum
    };
    state.tablesStatus = SUCCESS;
  },
  [GET_BUSINESS_LIST_FAILURE](state) {
    state.tables = {
      ...state.tables
    };
    state.tablesStatus = FAIL;
  },

  [SEARCH_BUSINESS_LIST](state, params) {
    let list = state.searchTables.list;

    if (params.offset === 0) {
      list = [];
    }
    state.searchTables = {
      list
    };
    state.tablesStatus = LOADING;
  },
  [SEARCH_BUSINESS_LIST_SUCCESS](state, payload) {
    state.searchTables = {
      list: payload.lists,
      total: payload.sum
    };
    state.tablesStatus = SUCCESS;
  },
  [SEARCH_BUSINESS_LIST_FAILURE](state) {
    state.searchTables = {
      ...state.searchTables
    };
    state.tablesStatus = FAIL;
  },

  [GET_BUSINESS_MANAGE_LIST](state, params) {
    let list = state.management.list;

    if (params.offset === 0) {
      list = [];
    }
    state.management = {
      list,
      status: LOADING
    };
  },
  [GET_BUSINESS_MANAGE_LIST_SUCCESS](state, payload) {
    state.management = {
      list: payload.lists,
      total: payload.sum,
      status: SUCCESS
    };
  },
  [GET_BUSINESS_MANAGE_LIST_FAILURE](state) {
    state.management = {
      ...state.management,
      status: FAIL
    };
  },

  [GET_BUSINESS_DEPARTMENTS](state, params) {
    let list = state.departments.list;

    state.departments = {
      list,
      status: LOADING
    };
  },
  [GET_BUSINESS_DEPARTMENTS_SUCCESS](state, payload) {
    state.departments = {
      list: payload,
      status: SUCCESS
    };
  },
  [GET_BUSINESS_DEPARTMENTS_FAILURE](state) {
    state.departments = {
      ...state.departments,
      status: FAIL
    };
  },

  [CREATE_BUSINESS_ITEM](state, params) {
    state.tableItemCreateStatus = LOADING;
  },
  [CREATE_BUSINESS_ITEM_SUCCESS](state, payload) {
    state.tableItemCreateStatus = SUCCESS;
  },
  [CREATE_BUSINESS_ITEM_FAILURE](state) {
    state.tableItemCreateStatus = FAIL;
  },

  [DELETE_BUSINESS_ITEM](state, params) {
    state.deleteStatus = LOADING;
  },
  [DELETE_BUSINESS_ITEM_SUCCESS](state, payload) {
    state.deleteStatus = SUCCESS;
  },
  [DELETE_BUSINESS_ITEM_FAILURE](state) {
    state.deleteStatus = FAIL;
  },

  [GET_BUSINESS_DETAIL](state, params) {
    state.businessDetail.status = LOADING;
  },
  [GET_BUSINESS_DETAIL_SUCCESS](state, payload) {
    state.businessDetail = {
      data: payload,
      status: SUCCESS
    };
  },
  [GET_BUSINESS_DETAIL_FAILURE](state) {
    state.businessDetail = {
      ...state.businessDetail,
      status: FAIL
    };
  },

  [GET_TECH_DETAIL](state, params) {
    state.techDetail.status = LOADING;
  },
  [GET_TECH_DETAIL_SUCCESS](state, payload) {
    state.techDetail = {
      data: payload,
      status: SUCCESS
    };
  },
  [GET_TECH_DETAIL_FAILURE](state) {
    state.techDetail = {
      ...state.techDetail,
      status: FAIL
    };
  },

  [UPDATE_BUSINESS_INFO](state, params) {
    state.businessUpdateStatus = LOADING;
  },
  [UPDATE_BUSINESS_INFO_SUCCESS](state, payload) {
    state.businessUpdateStatus = SUCCESS;
  },
  [UPDATE_BUSINESS_INFO_FAILURE](state) {
    state.businessUpdateStatus = FAIL;
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
};



